




































































import { Vue, Component } from 'vue-property-decorator'
import { isNull, isArray, isString } from 'plugin/helper'
import { Button, Icon } from 'vant'

import components from 'components/inside/components'
import sortableForm from '../../components/sortableForm.vue'
import shadowNode from '../../components/shadowNode.vue'

const removeNode = (node: HTMLElement) => {
  if (node.parentElement !== null) {
    node.parentElement.removeChild(node)
  }
}

const insertNodeAt = (fatherNode: HTMLElement, node: HTMLElement, position: number) => {
  const refNode = position === 0 ? fatherNode.children[0] : fatherNode.children[position - 1].nextSibling
  fatherNode.insertBefore(node, refNode)
}

@Component({
  name: 'index',
  components: {
    vanButton: Button,
    vanIcon: Icon,
    sortableForm,
    shadowNode,
    ...components
  }
})
export default class index extends Vue {
  private parent = window.parent.window

  private pageIndex: number = 1

  private data: Array<any> = []

  private get isNoneData() {
    const filterData = this.data.filter((item) => {
      if (item.configData.pageIndex) {
        return item.configData.pageIndex === this.pageIndex
      }
      return true
    })

    return isNull(filterData)
  }

  // 判断组件是否固定底部
  private componentIsFixedBottom(componentType: string) {
    const fixedComponentType: Array<string> = ['submitBar']
    return fixedComponentType.includes(componentType)
  }

  // 判断该组件是否显示
  private componentIsRegister(component: any, componentType: string) {
    const { type, configData } = component

    if (type === componentType && configData) {
      const { isHide, pageIndex, showLogicList } = configData
      // 显示的条件：不隐藏 并且 属于当前页 并且 计算 显示逻辑返回真
      return !isHide && pageIndex === this.pageIndex && this.showLogicComputed(showLogicList, 1)
    }

    return false
  }

  // 根据 showLogic 判断该组件是否显示
  /* type：
   1 是 showLogicList 中任一条件为真，结果就为真。
   2 是 showLogicList 中全部条件为真，结果才为真。
  */
  private showLogicComputed(showLogicList: Array<{ bind: string; value: string }>, type: 1 | 2) {
    // 是否显示
    let show: boolean = true

    // 如果为空直接返回
    if (isNull(showLogicList)) {
      return show
    }

    const recursion = (logicBind: string, logicValue: string): boolean => {
      const forms = this.data.filter((item) => item.type === 'form')

      for (let i = 0, l = forms.length; i < l; i++) {
        const { fieldList } = forms[i]
        for (let f = 0, l = fieldList.length; f < l; f++) {
          const { value, field } = fieldList[f]
          if (field === logicBind) {
            if (isArray(value) && value.includes(logicValue)) {
              return true
            }

            if (isString(value) && value === logicValue) {
              return true
            }
          }
        }
      }

      return false
    }

    for (let i = 0, l = showLogicList.length; i < l; i++) {
      const logic = showLogicList[i]
      // 计算当前逻辑结果返回是否为真
      show = recursion(logic.bind, logic.value)
      // 如果是类型是满足 "任一条件", 并且当前结果为真就立即返回真
      if (show && type === 1) return show
      // 如果是类型是满足 "全部条件", 并且当前结果为假就立即返回假
      if (!show && type === 2) return show
    }

    return show
  }

  private createdSortable() {
    const _this = this

    const el = document.getElementById('single-demo__body')

    if (this.parent && el) {
      this.parent.$emit('createdSortable', el, {
        group: {
          name: 'components'
        },
        onAdd(evt: any) {
          removeNode(evt.item)
          _this.parent.$emit('insertData', 'components', evt.item._underlying_vm_, evt.newIndex)
        },
        onUpdate(evt: any) {
          removeNode(evt.item)
          insertNodeAt(evt.from, evt.item, evt.oldIndex)
          _this.parent.$emit('updatePositionData', 'components', evt.newIndex, evt.oldIndex)
        }
      })
    }
  }

  private created() {
    // 等待父页面调用此方法提供预览数据
    this.parent.$on('updatePreviewData', (data: Array<any>, pageIndex: number, queryRule: any) => {
      this.pageIndex = pageIndex

      data.forEach((component: any) => {
        // 如果没有页数 默认是第一页
        if (!component.configData.pageIndex) component.configData.pageIndex = 1

        if (component.type === 'form') {
          component.configData.title = component.configData.title || '标题将在实际页面中隐藏'
        }
      })

      this.data = data
    })

    // 通知父页面 本页面已经创建完成
    this.parent.$emit('childrenPageStepUp')
  }

  private mounted() {
    this.createdSortable()
  }
}
