




















import { Vue, Component, Prop, Ref } from 'vue-property-decorator'
import { Icon } from 'vant'

interface settingInterface {
  type: 'components' | 'widgets'
  componentIndex: number | null
  widgetIndex: number | null
}

@Component({
  name: 'shadowNode',
  components: {
    vanIcon: Icon
  }
})
export default class shadowNode extends Vue {
  @Ref('shadowNodeWrapper') shadowNodeWrapperRef!: Element

  @Prop({ type: String }) type!: 'components' | 'widgets'
  @Prop({ type: Number }) componentIndex!: number
  @Prop({ type: Number }) widgetIndex!: number
  @Prop({ type: String }) emptyText!: string
  @Prop({ type: Boolean, default: false }) fixed!: boolean

  private parent = window.parent.window

  private setting: settingInterface = {
    type: 'components',
    componentIndex: null,
    widgetIndex: null
  }

  private updateSetting(componentIndex: number | null, widgetIndex: number | null) {
    this.setting.type = this.type
    this.setting.componentIndex = componentIndex
    this.setting.widgetIndex = widgetIndex
    this.parent.$emit('updateSetting', this.setting)
  }

  // 激活
  private activatedNode() {
    // 取消其余激活
    document.querySelectorAll('.shadow-node__wrapper').forEach((el) => {
      el.classList.remove('shadow-node__active')
    })

    // 激活当前
    this.shadowNodeWrapperRef.classList.add('shadow-node__active')
  }

  // 取消当前激活
  private deactivatedNode() {
    this.shadowNodeWrapperRef.classList.remove('shadow-node__active')
  }

  // 选中
  private checkedNode(componentIndex: number | null, widgetIndex: number | null, event: Event) {
    const el = event.target as Element

    let reset = this.shadowNodeWrapperRef.classList.contains('shadow-node__checked')

    // 取消所有选中
    document.querySelectorAll('.shadow-node__wrapper').forEach((el) => {
      el.classList.remove('shadow-node__checked')
    })

    // 选中当前
    !reset && this.shadowNodeWrapperRef.classList.add('shadow-node__checked')

    this.updateSetting(!reset ? componentIndex : null, !reset ? widgetIndex : null)
  }

  // 删除
  private deleteNode(componentIndex: number | null, widgetIndex: number | null) {
    this.updateSetting(null, null)
    this.parent.$emit('deleteData', this.type, componentIndex, widgetIndex)
  }
}
