

























































































import { Vue, Component, Prop } from 'vue-property-decorator'

import components from 'components/inside/components'
import shadowNode from './shadowNode.vue'

let uuid = 0

const removeNode = (node: HTMLElement) => {
  if (node.parentElement !== null) {
    node.parentElement.removeChild(node)
  }
}

const insertNodeAt = (fatherNode: HTMLElement, node: HTMLElement, position: number) => {
  const refNode = position === 0 ? fatherNode.children[0] : fatherNode.children[position - 1].nextSibling
  fatherNode.insertBefore(node, refNode)
}

@Component({
  name: 'sortbaleForm',
  components: {
    shadowNode,
    ...components
  }
})
export default class sortbaleForm extends Vue {
  @Prop({ type: Array, default: () => [] }) value!: Array<any>
  @Prop({ type: Number }) componentIndex!: number
  @Prop({ type: Function }) showLogicComputed!: Function

  private parent = window.parent.window

  private uuid: number = 0

  private get elementId() {
    return `sortbale-form_${this.uuid}`
  }

  // 判断该控件是否显示
  private widgetIsRegister(widget: any) {
    const { configData } = widget

    if (configData) {
      const { isHide, showLogicList } = configData
      // 显示的条件：不隐藏 并且 计算 显示逻辑返回真
      return !isHide && this.showLogicComputed(showLogicList, 1)
    }

    return false
  }

  private createdSortable() {
    const _this = this

    const el = document.getElementById(this.elementId)

    if (this.parent && el) {
      this.parent.$emit('createdSortable', el, {
        group: {
          name: 'widgets'
        },
        onStart(evt: any) {
          const index = evt.oldIndex || 0
          evt.item._underlying_vm_ = _this.value[index]
        },
        onAdd(evt: any) {
          removeNode(evt.item)
          _this.parent.$emit('insertData', 'widgets', evt.item._underlying_vm_, _this.componentIndex, evt.newIndex)
        },
        onUpdate(evt: any) {
          removeNode(evt.item)
          insertNodeAt(evt.from, evt.item, evt.oldIndex)
          _this.parent.$emit('updatePositionData', 'widgets', evt.newIndex, _this.componentIndex, evt.oldIndex)
        },
        onRemove(evt: any) {
          insertNodeAt(el, evt.item, evt.oldIndex)
          if (evt.pullMode === 'clone') {
            removeNode(evt.clone)
            return
          }
          _this.parent.$emit('deleteData', 'widgets', _this.componentIndex, evt.oldIndex)
        }
      })
    }
  }

  private mounted() {
    this.createdSortable()
  }

  private created() {
    uuid++
    this.uuid = uuid
  }
}
